import React, { Component } from 'react';
import "./table.css"
import {Table} from "antd"

class TableA extends Component {
    
   
      

    render() { 
        const columns = [
            {
                dataIndex: "course",
                title: "Courses Offered",
            },
            {
                dataIndex: "time",
                title: "Duration",
             
            },
            {
                dataIndex: "trainer",
                title: "Trainer",
              
            },
            {
                dataIndex: "price",
                title: "Price",
              
            },
          ];
          
          const data = [
            {
                key: 1,
                course: "HTML",
                time: "5DAYS",
                trainer: "AKASH",
                price: "Rs.100"
            },
            {
                key: 2,
                course: "CSS",
                time: "4DAYS",
                trainer: "JOSHITHA",
                price: "Rs.100"
            },
            {
                key: 3,
                course: "JAVASCRIPT",
                time: "10DAYS",
                trainer: "SOUMYA",
                price: "Rs.200"
            },
            {
                key: 4,
                course: "MYSQL",
                time: "3DAYS",
                trainer: "AKSHAT",
                price: "Rs.100"
            },
            {
                key: 5,
                course: "REACT",
                time: "10DAYS",
                trainer: "DHEERAJ",
                price: "Rs.300"
            },
            {
                key: 6,
                course: "NODEJS",
                time: "10DAYS",
                trainer: "NEELIMA",
                price: "Rs.300"
            },
            {
                key: 7,
                course: "DOUBTS SESSION",
                time: "LONG TERM ACCESS",
                trainer: "BHANU",
                price: "Rs.500"
            }
        ]
        
          
            function onChange(pagination, filters, sorter, extra) {
            console.log('params', pagination, filters, sorter, extra);
          }

        return ( <Table columns={columns} dataSource={data} onChange={onChange} className="table table-responsisve table-transparent"/> );
    }
}
 
export default TableA;